import './styles.css';


const toggle = document.querySelector('.nav-toggle');
const links = document.querySelector('.links');
const navBar = document.querySelector('#nav');
const type = document.querySelector('#typewriter');
const topLink = document.querySelector('#top-link');
const linksContainer = document.querySelector('.links-container');
const btn = document.querySelector('#darkMode');

const saveTheme = localStorage.getItem('theme')
if (saveTheme === 'light') {
    document.body.classList.add('light-mode');
    btn.textContent = '☀';
} else if (saveTheme === 'dark') {
    document.body.classList.add('dark-mode');
    btn.textContent = '🌙';
}
btn.addEventListener('click', ()=> {
    document.body.classList.toggle('light-mode');
    document.body.classList.toggle('dark-mode');

    const theme = document.body.classList.contains('light-mode')? 'light' : 'dark';
    btn.textContent = theme === 'light'? "☀" : '🌙';
    localStorage.setItem('theme', theme);
})

toggle.addEventListener('click', ()=> {
    links.classList.toggle('open-menu');
    // const containerHeight = linksContainer.getBoundingClientRect().height;
    // const linksHeight = links.getBoundingClientRect().height;
    // if (containerHeight === 0) {
    //     linksContainer.style.height = ${linksHeight}px;
    // } else {
    //     linksContainer.style.height = 0;
    // }
});

window.addEventListener('scroll', ()=> {
    if (window.scrollY > 100) {
        navBar.classList.add('nav-scrolled');
    } else {
        navBar.classList.remove('nav-scrolled');
    }
    if (window.scrollY > 200) {
        topLink.classList.add('show-link');
    } else {
        topLink.classList.remove('show-link')
    }
});
const scrollLinks = document.querySelectorAll('.scroll-link');
scrollLinks.forEach((link)=> {
    link.addEventListener("click", (event)=> {
        event.preventDefault();
        const id = event.currentTarget.getAttribute('href').slice(1);
        const element = document.getElementById(id);
        const navHeight = navBar.getBoundingClientRect().height;
        const containerHeight = linksContainer.getBoundingClientRect().height;
        const scrollnavBar = navBar.classList.contains('nav-scrolled');
        let position = element.offsetTop - navHeight;
        
        if (!scrollnavBar) {
            position = position - navHeight + containerHeight;
        }
        window.scrollTo({
            left: 0,
        top: position,
        });
        links.classList.remove('open-menu');
        linksContainer.style.height = 0;
    })
});

let slideIndex = 0;
showslide();

function showslide() {
    
    const slides = document.querySelectorAll('.myslides');
    const dots = document.querySelectorAll('.dot')

    for (let i  = 0; i < slides.length; i ++) {
        slides[i].style.display = "none";
    }
    //remove active dot
    for (let i  = 0; i < dots.length; i++) {
        dots[i].classList.remove('active')
    }
    //increament slide
    slideIndex ++;
if (slideIndex > slides.length) {
    slideIndex = 1;
}
slides[slideIndex-1].style.display = "block";
dots[slideIndex -1].classList.add('active');
setTimeout(showslide, 3000);
}

const skills = [
    'HTML5',
    'CSS3',
    'Javascript',
    'React',
    'Git & Github',
    'Responsive Design',
    'Webpack',
    'API Integration',
    'UI/UX Principles'
];

const container = document.querySelector('#skill-container');

skills.forEach(skill=> {
    const skillBox = document.createElement('div');
    skillBox.textContent = skill;
    skillBox.classList.add('skill-box');
    container.appendChild(skillBox);
});

container.style.display = 'grid';
container.style.gridTemplateColumns = 'repeat(2, 1fr)';
container.style.gap ='1rem';
container.style.padding = '2rem';

document.querySelectorAll('.skill-box').forEach(box => {
    box.style.backgroundColor = '#222';
    box.style.color = '#fff';
    box.style.padding = '1rem';
    box.style.textAlign = 'center';
    box.style.borderRadius = '8px';
    box.style.boxShadow = '0 2px 5px rgba(0, 0, 0 ,0.4)';
    box.style.transition = 'transform 0.3s ease';
    box.addEventListener('mouseover', ()=> {
        box.style.transform = 'scale(1.05)'
    });
    box.addEventListener('mouseout', ()=> {
        box.style.transform = 'scale(1)'
    });
}); 
