___CSS_LOADER_EXPORT___.push([module.id, ` * {
     padding: 0;
     margin: 0;
     box-sizing: border-box;
 }

 :root {
     --nav-color: rgb(7, 63, 7);
     --content-color: goldenrod;
     --text-color: #fff;
     --background-color: rgba(0, 0, 0, 0.856);
     --hover-color: rgba(7, 63, 7, 0.795);
 }

 body {
     font-family: 'poppins', sans-serif;
     background-color: white;
     color: black;
 }

 html {
     scroll-behavior: smooth;
 }

 /* dark mode toggle */
 body.dark-mode {
     color: #e0e0e0;
     background-color: #212121;
 }

 body.light-mode {
     color: #212121;
     background-color: #ffffff;
 }

 #nav {
     position: fixed;
     display: flex;
     top: 0;
     left: 0;
     width: 100%;
     justify-content: space-between;
     padding: 20px;
     transition: background-color 0.3s ease, box-shadow 0.3s ease;
     background-color: var(--content-color);
     z-index: 1000;
 }

 .banner {
     position: relative;
     padding: 50px 60px;
     max-width: 600px;
     margin-top: 250px;
     margin: 0 auto;
     z-index: 1;

 }


 .container img {
     width: 100%;
     height: auto;
     object-fit: cover;
     border-radius: 10px;
     clip-path: polygon(100% 0%, 0% 0%, 0% 100%, 95% 50%, 0% 0%, 40% 70%, 100% 100%);
 }


 .logo {
     font-family: 'poppins', serif;
     font-size: 1.2rem;
     color: var(--text-color);
     font-weight: bold;
 }

 a {
     text-decoration: none;
     color: var(--text-color);
     font-size: 1.1rem;
 }

 a:hover::after {
     content: '';
     display: block;
     width: 100%;
     height: 2px;
     background-color: var(--text-color);
     transition: width 0.3s;
 }

 .links {
     display: flex;
     gap: 20px;
     color: var(--text-color);
     font-size: 1.1rem;
 }

 ul li {
     list-style: none;

 }

 .nav-toggle {
     display: none;
     border: none;
     font-size: 1.5rem;
     color: var(--text-color);
 }

 .container {
     height: 100vh;
     width: 100%;
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     text-align: center;
     color: var(--content-color);
 }

 .container p {
     color: var(--nav-color);
 }

 .port-btn {
     padding: 10px 15px;
     margin-top: 10px;
 }

 .port-btn:hover {
     transition: 0.4s ease-in-out;
     border-radius: 10px;
 }

 .nav-scrolled {
     background-color: rgb(7, 63, 7);
 }

 .section {
     margin: 0 auto;
     padding: 30px 50px;

 }

 .title {
     justify-content: center;
     place-items: center;
     text-transform: uppercase;
 }


 .footer {
     display: grid;
     grid-template-columns: repeat(2, 1fr);
 }

 .foot h3 {

     margin-bottom: 10px;
 }

 .foot {
     margin: 20px;
     width: 50px;
 }

 .foot a {
     gap: 20px;
 }

 .icons {
     display: flex;
     flex-direction: column;
     gap: 20px;
     margin-top: 20px;
     margin: 20px;
     font-size: 1.5rem;
     cursor: pointer;
 }
 .icons a:hover {
    width: 0;
 }

 .show-link {
     visibility: visible;
     opacity: 1;
     z-index: 100;
 }

 .top-link {
     position: fixed;
     font-size: 1.6rem;
     bottom: 3rem;
     right: 3rem;
     border-radius: 10px;
     width: 2rem;
     height: 3rem;
     display: grid;
     place-items: center;
     visibility: hidden;

     z-index: 100;
 }

 .about-content {
     display: grid;
     grid-template-columns: repeat(2, 1fr);
 }

 .image {
     max-width: 600px;
     padding: 20px;
 }

 .image img {
     width: 100%;
     height: auto;
     object-fit: contain;
     border-radius: 10px;
 }

 .content {
     justify-content: center;
     text-align: center;
     margin-top: 20px;
     background-color: #faf8f8f1;
     color: rgba(0, 0, 0, 0.74);
     border-radius: 10px;

 }

 .content p {
     margin: 20px;
 }

 .title h2 {
     box-shadow: 0 0 10px black;
     margin-bottom: 10px;
     color: var(--content-color);
 }

 .title p {
     text-transform: lowercase;
 }

 .service-container {
     display: grid;
     grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
     padding: 2rem 0;
     gap: 2rem;
 }

 .service {
     background-color: #f4f4f4d7;
     padding: 2rem;
     color: black;
     border-radius: 10px;
     box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
     transition: transform 0.3s ease;
 }

 .service h3 {
     text-transform: uppercase !important;
 }
.service p::first-letter {
  text-transform: uppercase;
}

 .service:hover {
     transform: translateY(-5px);
 }

 .service h3 {
     margin-bottom: 1rem;
     color: var(--content-color);
     text-transform: lowercase;
 }

 .social {
     position: absolute;
     display: flex;
     flex-direction: column;
     align-items: center;
     gap: 20px;
     margin-top: 10px;
     left: 0;
 }

 .social a {
     font-size: 2rem;
     color: var(--content-color);
 }

 .social i:hover {
     background-color: #e0e0e0;
     transition: 0.3s ease-in-out;
 }

 .slideshow-container {
     max-width: 700px;
     position: relative;
     margin: auto;
 }

 .myslides {
     display: none;
 }

 .myslides img {
     width: 100%;
     height: auto;
     object-fit: cover;
 }

 .prev,
 .next {
     cursor: pointer;
     position: absolute;
     top: 50%;
     width: auto;
     margin-top: -22px;
     padding: 16px;
     border: none;
     font-weight: bold;
     font-size: 18px;
     transition: 0.6s ease;
     border-radius: 0 3px 3px 0;
     user-select: none;
 }

 .next {
     border-radius: 3px 0 0 3px;
     right: 0;
 }

 .text {
     color: white;
     font-size: 15px;
     padding: 8px 12px;
     position: absolute;
     bottom: 8px;
     width: 100%;
     text-align: center;
 }

 .numberText {
     color: white;
     font-size: 12px;
     padding: 8px 12px;
     position: absolute;
     top: 0;
 }

 .dots {
     display: flex;
     justify-content: center;
     align-items: center;
     text-align: center;

 }

 .dot {
     cursor: pointer;
     height: 15px;
     width: 15px;
     margin: 0 3px;
     background-color: var(--content-color);
     border-radius: 50%;
     display: block;
     transition: background-color 0.6s ease;
 }

 .active,
 .dot:hover {
     background-color: rgb(146, 141, 141);
 }

 .fade {
     animation: fade;
     animation-duration: 1.5s;
 }

 #darkMode {
     position: absolute;
     top: 5px;
     right: 20px;
 }

 #darkMode button {
     border: none;
     border-radius: 50%;
 }
 #darkMode button i {
     font-size: 1.5rem;
 }

 .contact-section {
     padding: 4rem 2rem;
     text-align: center;
 }

 .contact-section p {
     margin-bottom: 10px;
 }

 #contact-form {
     background-color: #ffffffd7;
     max-width: 500px;
     margin: 0 auto;
     display: flex;
     flex-direction: column;
     gap: 1rem;
 }

 #contact-form input,
 #contact-form textarea {
     padding: 0.8rem;
     border: 1px solid #ccc;
     border-radius: 5px;
     font-size: 1rem;
     width: 100%;
     resize: none;
 }

 #contact-form button {
     padding: 0.8rem;
     background-color: #007bff;
     color: white;
     width: 50%;
     border: none;
     border-radius: 7px;
     cursor: pointer;

 }

 #contact-form button:hover {
     background-color: #007bffda;
 }


 @keyframes fade {
     from {
         opacity: .4;
     }

     to {
         opacity: 1;
     }
 }

 @media (prefers-color-scheme: dark) {
     body {
         color: white;
         background: black;
     }

     body.light-mode {
         color: black;
         background-color: white;
     }
 }

 @media (max-width: 768px) {
     .links-container {
         height: 0;
         left: 0;
         overflow: hidden;
         width: 100%;
         transition: height 0.3s ease;
     }

     .links {
         position: absolute;
         display: flex;
         flex-direction: column;
         right: 0;
         top: 100px;
         transform: translateX(100%);
         height: 100vh;
         background-color: var(--content-color);
         opacity: 0.9;
         transition: 0.3s ease-in-out;
         width: 50%;
         top: 90px;
         text-align: center;
     }

     .nav-toggle {
         position: absolute;
         top: 20px;
         right: 20px;
         display: block;
         background: transparent;
         cursor: pointer;
         z-index: 100;
     }

     .links.open-menu {
         transform: translateX(0);

     }

     .banner {
         padding: 20px 60px;
         margin-top: 0;
     }

     #nav {
         margin-bottom: 0;
     }

     #darkMode {
         top: 25px;
         right: 60px;

     }

     .social {
         left: 0;
         margin-top: 10%;

     }

     .about-content {
         grid-template-columns: 1fr;
     }
 }`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"CAAC;KACI,UAAU;KACV,SAAS;KACT,sBAAsB;CAC1B;;CAEA;KACI,0BAA0B;KAC1B,0BAA0B;KAC1B,kBAAkB;KAClB,wCAAwC;KACxC,oCAAoC;CACxC;;CAEA;KACI,kCAAkC;KAClC,uBAAuB;KACvB,YAAY;CAChB;;CAEA;KACI,uBAAuB;CAC3B;;CAEA,qBAAqB;CACrB;KACI,cAAc;KACd,yBAAyB;CAC7B;;CAEA;KACI,cAAc;KACd,yBAAyB;CAC7B;;CAEA;KACI,eAAe;KACf,aAAa;KACb,MAAM;KACN,OAAO;KACP,WAAW;KACX,8BAA8B;KAC9B,aAAa;KACb,4DAA4D;KAC5D,sCAAsC;KACtC,aAAa;CACjB;;CAEA;KACI,kBAAkB;KAClB,kBAAkB;KAClB,gBAAgB;KAChB,iBAAiB;KACjB,cAAc;KACd,UAAU;;CAEd;;;CAGA;KACI,WAAW;KACX,YAAY;KACZ,iBAAiB;KACjB,mBAAmB;KACnB,+EAA+E;CACnF;;;CAGA;KACI,6BAA6B;KAC7B,iBAAiB;KACjB,wBAAwB;KACxB,iBAAiB;CACrB;;CAEA;KACI,qBAAqB;KACrB,wBAAwB;KACxB,iBAAiB;CACrB;;CAEA;KACI,WAAW;KACX,cAAc;KACd,WAAW;KACX,WAAW;KACX,mCAAmC;KACnC,sBAAsB;CAC1B;;CAEA;KACI,aAAa;KACb,SAAS;KACT,wBAAwB;KACxB,iBAAiB;CACrB;;CAEA;KACI,gBAAgB;;CAEpB;;CAEA;KACI,aAAa;KACb,YAAY;KACZ,iBAAiB;KACjB,wBAAwB;CAC5B;;CAEA;KACI,aAAa;KACb,WAAW;KACX,aAAa;KACb,sBAAsB;KACtB,uBAAuB;KACvB,mBAAmB;KACnB,kBAAkB;KAClB,2BAA2B;CAC/B;;CAEA;KACI,uBAAuB;CAC3B;;CAEA;KACI,kBAAkB;KAClB,gBAAgB;CACpB;;CAEA;KACI,4BAA4B;KAC5B,mBAAmB;CACvB;;CAEA;KACI,+BAA+B;CACnC;;CAEA;KACI,cAAc;KACd,kBAAkB;;CAEtB;;CAEA;KACI,uBAAuB;KACvB,mBAAmB;KACnB,yBAAyB;CAC7B;;;CAGA;KACI,aAAa;KACb,qCAAqC;CACzC;;CAEA;;KAEI,mBAAmB;CACvB;;CAEA;KACI,YAAY;KACZ,WAAW;CACf;;CAEA;KACI,SAAS;CACb;;CAEA;KACI,aAAa;KACb,sBAAsB;KACtB,SAAS;KACT,gBAAgB;KAChB,YAAY;KACZ,iBAAiB;KACjB,eAAe;CACnB;CACA;IACG,QAAQ;CACX;;CAEA;KACI,mBAAmB;KACnB,UAAU;KACV,YAAY;CAChB;;CAEA;KACI,eAAe;KACf,iBAAiB;KACjB,YAAY;KACZ,WAAW;KACX,mBAAmB;KACnB,WAAW;KACX,YAAY;KACZ,aAAa;KACb,mBAAmB;KACnB,kBAAkB;;KAElB,YAAY;CAChB;;CAEA;KACI,aAAa;KACb,qCAAqC;CACzC;;CAEA;KACI,gBAAgB;KAChB,aAAa;CACjB;;CAEA;KACI,WAAW;KACX,YAAY;KACZ,mBAAmB;KACnB,mBAAmB;CACvB;;CAEA;KACI,uBAAuB;KACvB,kBAAkB;KAClB,gBAAgB;KAChB,2BAA2B;KAC3B,0BAA0B;KAC1B,mBAAmB;;CAEvB;;CAEA;KACI,YAAY;CAChB;;CAEA;KACI,0BAA0B;KAC1B,mBAAmB;KACnB,2BAA2B;CAC/B;;CAEA;KACI,yBAAyB;CAC7B;;CAEA;KAC